.App {
  text-align: center;
}

.App-header {
  background-color: #FFD6BA;
  text-align: center;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(60px + 2vmin);
  font-family:'Bentham', serif;
  color: #555B6E;
  font-style: italic;
  font-weight: bold;
  text-transform: uppercase;
  /* text-shadow: -1px 0 #000000, 0 1px #000000, 1px 0 #000000, 0 -1px #000000,
               -5px 0px #555B6E,
               -6px 1px #000000, -6px -1px #000000; */
}

.Header {
  margin-bottom: 10px;
}

.content {
  min-height: 100vh;
}

  .center {
    text-align: center;
  }
  
  /* NAVIGATION */
  nav {
    min-height: 10vh;
    background-color: #FFD6BA;
    /* background-image: linear-gradient(#FFD6BA, #FFD6BA, white, white); */
    font-size: calc(8px + 2vmin);
    font-family:'Bentham', serif;
  }
  
  nav ul {
    list-style: none;
    text-align: center;
  }
  nav ul li {
    display: inline-block;
  }
  nav ul li a {
    display: block;
    padding: 15px;
    padding-bottom: 2px;
    text-decoration: none;
    color: #555B6E;
    font-weight: 400;
    text-transform: lowercase;
    margin: 0 10px;
  }
  nav ul li a,
  nav ul li a:after,
  nav ul li a:before {
    transition: all .5s;
  }
  nav ul li a:hover {
    color: palevioletred;
    text-decoration: none;
  }
  
  /* stroke */
  nav.stroke ul li a,
  nav.fill ul li a {
    position: relative;
  }
  nav.stroke ul li a:after,
  nav.fill ul li a:after {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 0%;
    content: '.';
    color: transparent;
    background: palevioletred;
    height: 1px;
  }
  nav.stroke ul li a:hover:after {
    width: 100%;
  }
  
  nav.fill ul li a {
    transition: all 2s;
  }
  
  nav.fill ul li a:after {
    text-align: left;
    content: '.';
    margin: 0;
    opacity: 0;
  }
  nav.fill ul li a:hover {
    color: #023f1c;
    z-index: 1;
  }
  nav.fill ul li a:hover:after {
    z-index: -10;
    animation: fill 1s forwards;
    -webkit-animation: fill 1s forwards;
    -moz-animation: fill 1s forwards;
    opacity: 1;
  }
  

.top-right {
    position: absolute;
    top: 0px;
    right: 0px;
    padding-right: 3%;
    padding-top: 1%;
